<template>
  <div class="home">
    <div class="home-box">
      <div class="home-img">
        <img :src="imgUrl" />
        <div v-if="isCodeState == 3" class="masking" @click="refresh">
          <p>二维码失效<br />请点击刷新</p>
        </div>
      </div>
      <div v-if="isCodeState == 2" class="btn-box">
        <button class="btn" @click="skip">确定</button>
      </div>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
export default {
  name: "Home",
  data() {
    return {
      imgUrl: "",
      qrId: "",
      isCodeState: 0,
      timer: null,
    };
  },
  created() {
    this.getCode();
  },
  // destroyed() {
  //   clearInterval(this.timer);
  // },
  methods: {
    // 获取二维码图片
    getCode() {
      this.$axios
        .get("/qrScan")
        .then((res) => {
          if (res) {
            this.imgUrl = res.data.qrcodeUrl;
            this.qrId = res.data.qrId;
            this.getCodeType();
          }
        })
        .catch((e) => {
          console.log(e);
        });
    },

    // 获取二维码状态
    async getCodeType() {
      let res = await this.$axios.get("/qrStatus", {
        params: {
          qrId: this.qrId,
        },
      });
      if (res) {
        this.isCodeState = res.data.state;
        // this.isCodeState = 2;
        if (res.data.state === 3) {
          setTimeout(() => {
            this.getCode();
          }, 1000);
        } else {
          setTimeout(() => {
            this.getCodeType();
          }, 500);
        }
      }
    },

    //
    // inquireData() {
    //   if(this.timer){
    //     clearInterval(this.timer);
    //   }

    //   // 轮询中
    //   this.timer = setInterval(() => {
    //     this.getCodeType();
    //     console.log(1212);
    //     // let aaa = this.isCodeState;
    //     this.isCodeState +=1
    //     // if (this.isCodeState == 3 && aaa != 3) {
    //     //   clearInterval(this.timer);
    //     //   setTimeout(() => {
    //     //     this.getCode();
    //     //   }, 2000);
    //     // }
    //   }, 500);
    // },

    // 刷新页面
    refresh() {
      this.getCode();
    },

    // 验证
    async skip() {
      window.location.href=`http://192.168.2.20:9091/qrVerify?qrId=${this.qrId}`;
      // await this.$axios.get("/api/qrVerify", {
      //   params: {
      //     qrId: this.qrId,
      //   },
      // });
    },
  },
};
</script>

